import { useAuth } from '../hooks/useAuth';
import { useHistory } from "react-router-dom";

import illustrationImg from "../assets/images/illustration.svg";
import logoImg from "../assets/images/logo.svg";
import googleIconImg from "../assets/images/google-icon.svg"

import { Button } from "../components/Button";

import '../styles/auth.scss'
import { FormEvent, useState } from 'react';
import { database } from '../services/firebase';
import { Reference } from '../utilities/Reference';
import { child } from '@firebase/database';


function Home() {
    const history = useHistory();
    const {user, singInWithGoogle} = useAuth()
    const [roomCode, setRoomCode] = useState('')

    async function handleCreateRoom() {
        if (!user) {
            await singInWithGoogle()
            history.push('/rooms/new')
        } else {
            history.push('/rooms/new')
        }
    }

    async function handleJoinRoom(event: FormEvent) {
        event.preventDefault()

        if (roomCode.trim() === '') {
            return
        }

        const roomRef = Reference(`rooms/${roomCode}`)
        const roomRefExists = (await database.get(roomRef)).exists()
        
        if (!roomRefExists) {
            alert('Room does not exists')
            return
        }

        const endedAtExists = (await database.get(child(roomRef, "endedAt"))).exists()
        console.log(endedAtExists)

        if(endedAtExists) {
            alert('Room alredy closed.')
            return
        }

        history.push(`/rooms/${roomCode}`)
    }

    return (
        <div id="page-auth">
            <aside>
                <img src={illustrationImg} alt="Ilustração simbolizando perguntas e respotas" />
                <strong>Cria salas de Q&amp;A ao vivo</strong>
                <p>Tire as dúvidas da sua audiência em tempo-real</p>
            </aside>
            <main>
                <div className="main-content">
                    <img src={logoImg} alt="Letmeask" />
                    <button className="create-room" onClick={handleCreateRoom}>
                        <img src={googleIconImg} alt="Logo do Google" />
                        Crie sua sala com o Google
                    </button>
                    <div className="separetor">ou entre em uma sala</div>
                    <form onSubmit={handleJoinRoom}>
                        <input 
                            type="text" 
                            placeholder="Digite o código da sala"
                            onChange={event => setRoomCode(event.target.value)}
                            value={roomCode}
                        />
                        <Button type="submit">
                            Entrar na sala
                        </Button>
                    </form>
                </div>
            </main>
        </div>
    )
}

export {Home}